import * as EvtsActionType from '../actionType/evtsActionType';
export var fetchEvts = function (date) { return ({
    type: EvtsActionType._FETCH_EVTS,
    payload: { date: date },
}); };
export var fetchMonthEvts = function (year, month) { return ({
    type: EvtsActionType._FETCH_MONTH_EVTS,
    payload: { year: year, month: month + 1 },
}); };
export var fetchEvtsOfDates = function (dates) { return ({
    type: EvtsActionType._FETCH_EVTS_OF_DATES,
    payload: { dates: dates },
}); };
export var saveEvt = function (evt) { return ({
    type: EvtsActionType._SAVE_EVT_,
    payload: { evt: evt },
}); };
export var resetSaveEvtStatus = function () { return ({
    type: EvtsActionType._RESET_SAVE_STATUS,
}); };
export var updateEvt = function (id, updates, originalType) { return ({
    type: EvtsActionType._UPDATE_EVT,
    payload: {
        originalType: originalType,
        updates: updates,
        id: id,
    },
}); };
export var deleteEvt = function (id, type) { return ({
    type: EvtsActionType._DELETE_EVT,
    payload: {
        type: type,
        id: id,
    },
}); };
