import { useEffect } from 'react';
var ClickOutSider = function (props) {
    var isMouseEvtOutSide = function (evt) {
        var getContainer = props.getContainer, cb = props.cb;
        var target = evt.target;
        var container = getContainer();
        if (!container.contains(target)) {
            cb();
        }
    };
    useEffect(function () {
        document.addEventListener('click', isMouseEvtOutSide, false);
        return function () {
            document.removeEventListener('click', isMouseEvtOutSide, false);
        };
    }, []);
    return null;
};
export default ClickOutSider;
