export function getEvtCxtMenuProps(item) {
    if (item.type === 'activity') {
        return {
            isRightClickable: true,
            id: item._id,
            color: item.opts
                .color,
        };
    }
    else {
        var reminderModel = item;
        return {
            isRightClickable: reminderModel.reminders.length === 1,
            id: reminderModel.reminders[0]._id,
            color: reminderModel.reminders[0].opts.color,
        };
    }
}
export function getIdFromSortedEvt(evt) {
    if (evt.reminders) {
        return evt.reminders[0]
            ._id;
    }
    else {
        return evt._id;
    }
}
