import { convertTimeFormatToDate, convertMinAddToDate, } from '../../../utils/timeRangeHelper';
import CalConfig from '../../../assets/scripts/calendar.config';
export var getInitActivityModel = function () {
    return {
        description: '',
        address: '',
        color: CalConfig.colors.alldayActivityColor,
    };
};
export var getInitReminderModel = function () {
    return {
        repeatOption: 'everySameDate',
        color: CalConfig.colors.reminderColor,
    };
};
export var getTimePickerItems = function (initTiming, intl) {
    console.log('getTimePickerItems');
    var hourSplitter = CalConfig.hourSplitter, nbTimingPickerOptions = CalConfig.nbTimingPickerOptions;
    var minAddUnit = Math.round(60 / hourSplitter);
    var datesForTiming = [];
    var initDate = convertTimeFormatToDate(initTiming);
    var offset;
    for (var i = 0; i < nbTimingPickerOptions; i++) {
        var offsetVal = minAddUnit * i;
        offset =
            offsetVal < 60
                ? intl.formatMessage({ id: 'cal.minOffset' }, {
                    offset: offsetVal,
                })
                : intl.formatMessage({ id: 'cal.hourOffset' }, {
                    offset: offsetVal / 60,
                });
        datesForTiming.push({
            date: convertMinAddToDate(initDate, minAddUnit * i),
            offset: offset,
        });
    }
    return datesForTiming;
};
export var updateDateForDBTiming = function (value, origin) {
    return {
        year: value.getFullYear(),
        month: value.getMonth() + 1,
        dayAt: value.getDate(),
        hourAt: origin ? origin.hourAt : value.getHours(),
        minAt: origin ? origin.minAt : value.getMinutes(),
    };
};
